  }
} */

@media screen and (max-width: 600px) {
  .material-icons {
    display: none;
    color: red;
  }
  .projects-grid {
    flex-direction: column;
  }
  .banner-text {
    width: 90%;
    justify-content: space-between;
    /*  margin: auto; */
    /*  padding: 8px; */
  }
  .banner-text h1 {
    font-size: 40px;
  }
  .social-links {
    width: 100%;
  }
  .social-links i {
    color: red;
    font-size: 4em;
    padding: 8px;
  }

  .mdl-list__item .mdl-list__item-primary-content {
    font-size: 16px !important;
    color: white;
  }
  .contact-list i {
    padding-right: 1rem;
  }
  .mdl-list__item {
    padding: 0% !important;
    letter-spacing: 0em !important;
  }
}
